import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../services/header.service';
import { SharedService } from '../../../../services/shared.service';
import { ModalEditComponent } from './components/modal-edit/modal-edit.component';
import { CloseRegisterService } from './services/close-register.service';
import * as _ from 'underscore';
import { ModalCloseComponent } from './components/modal-close/modal-close.component';
import { ModalMessageComponent } from '../../../../shared/components/modal-message/modal-message.component';
import { HttpserviceService } from '../../../../services/httpservice.service';
import { CommonService } from '../../../../services/common.service';
import { SubjectService } from '../../../../services/subject.service';
import { object } from 'underscore';

@Component({
  selector: 'app-close-register',
  templateUrl: './close-register.component.html',
  styleUrls: ['./close-register.component.scss'],
})
export class CloseRegisterComponent implements OnInit {
  isShow: boolean = true;
  isClose: boolean;
  isNoOPenSession: boolean;

  items = [1, 2, 1, 2, 1, 2];
  formGroup: FormGroup;
  submitted = false;

  registerName: string;
  registerId: string;
  branchName: string;
  cityName: string;

  currentDate = new Date();
  selected: number = 1;
  selectedTab = 'cash';
  // registerId: number;
  intervalId;
  pageNo: number = 0;
  pagin!: number;
  pages!: number[];
  closingNotes: string;
  errorMessage = '';
  closeSessionTabs = { cash: true, cheque: false, cards: false, bankTransfer: false, bankPledge: false };
  closingData;
  ServicesSalesOrders: any;
  DirectPaymentSalesOrders: any;
  registerData: any;
  isNoOPenSessionMessage: string;
  @ViewChild('errorModal') errorModal: ElementRef;
  constructor(
    public closeRegisterService: CloseRegisterService,
    private modalService: NgbModal,
    public sharedService: SharedService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    public headerService: HeaderService,
    config: NgbModalConfig,
    private httpserviceService: HttpserviceService,
    private _commonService: CommonService, public _subjectService: SubjectService


  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    this.headerService.setTitle('Close Register');

    this.registerName =
      this.sharedService.getRegister?.registerObj?.registersName;
    this.registerId = this.sharedService.getRegister?.registerObj?.id;

    this.branchName =
      this.sharedService?.getRegister?.registerObj.registeBranch.branchName;
    this.cityName =
      this.sharedService?.getRegister?.registerObj.registeBranch.city;
    this.getForm();
    this.GetRegisterClosingData();

    this.intervalId = setInterval(() => {
      this.currentDate = new Date();
    }, 1000);
  }

  getForm() {
    this.formGroup = this.fb.group({
      registerId: this.registerId,
      closingNotes: 0,
      registerSessionClosingAmounts: this.fb.array([]),
    });
  }
  goBack() {
    this.isClose = false;
    const tabKeys = Object.keys(this.closeSessionTabs);
    tabKeys.forEach(v => this.closeSessionTabs[v] = false);
    this.closeSessionTabs.cash = true;
    this.selectedTab = tabKeys[0];
  }
  AddCurruncys() {
    return this.fb.group({
      currencyID: 0,
      registeredAmount: 0,
      actualAmount: [null, Validators.min(1)],
      differenceAmount: 0,
      currencyName: '',
    });
  }
  get registerSessionClosingAmounts() {
    return this.formGroup.controls[
      'registerSessionClosingAmounts'
    ] as FormArray;
  }
  isCheque: boolean;
  isCard: boolean;
  chooseTab(val: string) {
    if (val) {
      let keys = Object.keys(this.closeSessionTabs)
      //let lastItem = keys.pop();
      ////if (this.closeSessionTabs[lastItem]) {
      ////  this.GetRegisterClosingData();
      ////  this.isClose = true;
      ////}
      keys.forEach(v => this.closeSessionTabs[v] = false);
      this.closeSessionTabs[val] = true;
      this.selectedTab = val;
    }
    else {
      this.getRegisterClosingDataFinal();
      this.isClose = true;
    }
  }
  getRegisterClosingDataFinal() {
    this.closeRegisterService.getRegisterClosingDataFinal().subscribe(response => {
      console.log(response);
      if (response.isSuccess) {
        this.closingData = response.data;
      }
    })
  }
  GetRegisterClosingData() {
    return this.closeRegisterService
      .GetRegisterClosingData()
      .subscribe((response: any) => {
        if (response) {
          this.registerData = response.data;
          console.log(response);
          if (this.registerSessionClosingAmounts.length == 0) {
            for (let w = 0; w < this.registerData?.cashCollected.length; w++) {
              this.registerSessionClosingAmounts.push(
                this.fb.group({
                  registeredAmount:
                    this.registerData.cashCollected[w].collectedAmount,
                  actualAmount: null,
                  differenceAmount: 0,
                  currencyID: this.registerData.cashCollected[w].currencyId,
                  currencyName:
                    this.registerData.cashCollected[w]?.currencyName[0]
                      .lookupName,
                })
              );
            }
          }
          if (response?.Errors) {
            console.log(response.Errors);
            console.log(response.Errors[0].StatusCode);
            if (response.Errors[0].StatusCode == 82) {
              this.isNoOPenSession = true;
              this.isNoOPenSessionMessage = response?.Errors[0]?.ErrorMessageEn;
            }
          }
        }
      }, (error => {
        this.errorMessage = error['error']?.Errors[0]?.ErrorMessageEn;
      }));
  }
  loadPageData(event) {
    this.GetRegisterClosingData();
  }
  actualAmountFocusOut(control, index) {
    let formGroup = (this.formGroup.get('registerSessionClosingAmounts') as FormArray).at(index) as FormGroup;
    let value = +formGroup.controls[control].value;
    if (value > 0) {
      formGroup.controls[control].setValue(value.toFixed(3));
    }
  }
  calcDifference(event, i) {
    const mval = event.target.value;
    this.formGroup.get('registerSessionClosingAmounts')['at'](i).get('differenceAmount').patchValue(this.formGroup.get('registerSessionClosingAmounts')['at'](i).get('registeredAmount').value - mval);
  }
  removeMinusFromNegativeValue(value) {
    return Math.abs(value);
  }
  openModalEdit() {
    const modalRef = this.modalService.open(ModalEditComponent);
    modalRef.componentInstance.name = 'add';
  }

  nextStep() {
    //if (this.formGroup.invalid) {
    //  this.formGroup.markAllAsTouched();
    //  return window.scroll(0, 0);
    //}
    const tabKeys = Object.keys(this.closeSessionTabs);
    for (let i = 0; i < tabKeys.length; i++) {
      let key = tabKeys[i]
      if (this.closeSessionTabs[key]) {
        let nextKey = tabKeys[i + 1];
        this.chooseTab(nextKey);
        break;
      }
    }
    //if (this.selected == 1) {
    //  this.submitted = true;
    //  if (this.formGroup.invalid) {
    //    return window.scroll(0, 0);
    //  }
    //  if (this.formGroup.invalid) {
    //    return window.scroll(0, 0);
    //  }

    //  this.selected = 2;
    //} else if (this.selected == 2) {
    //  this.selected = 3;
    //} else if (this.selected == 3) {
    //  this.GetRegisterClosingData();
    //  this.isClose = true;
    //}
  }
  remainedSessions: number;
  getUserMenu() {
    this.httpserviceService.GetUserMenuNew(17001).subscribe((response: any) => {
      let userMenu: any[] = response?.data?.menu;
      for (let menu of userMenu) {
        let findItem = menu.items.find(x => x.canClick);
        if (findItem) {
          this._commonService.NavigateToRoute(findItem.url);
          console.log(findItem);
          break;
        }
      }
    })
  }

  CloseRegisterSession() {
    if (this.formGroup.invalid) {
      this.goBack();
      this.formGroup.markAllAsTouched();
      return false;
    }
    this.formGroup.get('closingNotes').patchValue(this.closingNotes);
    return this.closeRegisterService
      .CloseRegisterSession(this.formGroup.value)
      .subscribe((response: any) => {
        if (response.isSuccess) {
          this.remainedSessions = response.info.remainedSessions;
          console.log(response);
          const modalRef = this.modalService.open(ModalCloseComponent);
          modalRef.componentInstance.type = 'success';
          modalRef.componentInstance.message = 'Close session done successfully';
          modalRef.componentInstance.remainedSessions = response.info.remainedSessions;
          modalRef.componentInstance.routeName = '/close-register';
          this._subjectService.isRegisterOpen.next(true);
          this._subjectService.sessionDetails.next({ opened: false });
          //this.getUserMenu();
          modalRef.componentInstance?.loadPageData?.subscribe((result: any) => {
            console.log('result', result);
            localStorage.removeItem('closeSession');

          });
        } else {
          console.log('error', response);
          const modalRef = this.modalService.open(ModalCloseComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = response.Errors;
          modalRef.componentInstance.routeName = '/close-register';
          modalRef.componentInstance.backToSecond.subscribe((result: any) => {
            console.log('result', result);
            this.modalService.dismissAll();
            this.isClose = false;
            this.selected = 2;
          });
        }
      }, error => {
        this.errorMessage = error?.error?.errors[0]?.ErrorMessageEn
        //this.modalService.open(this.errorModal);
        setTimeout(() => {
          //this.modalService.dismissAll();
          this.errorMessage = '';
        }, 4000)
      });
  }
}
