import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SharedService } from '../../../../../../services/shared.service';
import { CloseRegisterService } from '../../services/close-register.service';
import * as _ from 'underscore';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup } from '@angular/forms';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { ModalEditCardComponent } from '../modal-edit-card/modal-edit-card.component';
import { ConfirmModalComponent } from '../../../../../../shared/components/confirm-modal/confirm-modal.component';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-collect-card',
  templateUrl: './collect-card.component.html',
  styleUrls: ['./collect-card.component.scss'],
})
export class CollectCardComponent {
  pageNo: number = 1;
  pagin!: number;
  pages!: number[];
  searchText: string = '';
  formGroup: FormGroup;
  collectedData: any;
  totalAllRecordsCount: number;
  totalRecordsCount: number;
  groupActionFlag = false;
  cardCollectionIds = '';
  @Output() loadMainData = new EventEmitter<string>();

  constructor(
    public closeRegisterService: CloseRegisterService,
    public sharedService: SharedService,
    private modalService: NgbModal,
    private _spinnerService: NgxSpinnerService,
  ) { }

  ngOnInit(): void {
    this.GetCurrentSessionCollectedCard();
    this.getCurrentSessionCollectedCardAll();
  }
  confirmGrouping() {
    if (!this.cardCollectionIds) return;
    const ref = this.modalService.open(ConfirmModalComponent, { centered: true });
    ref.componentInstance.data = { body: 'Are you sure you want to accept all cards?' };
    ref.componentInstance.eventData.subscribe(x => {
      if (x) {
        this.groupActionsOnCard();
      }
    })
  }
  getCurrentSessionCollectedCardAll() {
    return this.closeRegisterService
      .GetCurrentSessionCollectedCard('', 1, 1000)
      .subscribe((response: any) => {
        if (response) {
          let data = response.data;
          this.groupActionFlag = data.every(x => x.actionTaken);
          this.cardCollectionIds = data.map(x => x.ordersCardsCollectionId).toString();
        }
      });
  }
  groupActionsOnCard() {
    this._spinnerService.show();
    const formData = new FormData();
    formData.append('OrdersCardsCollectionIds', this.cardCollectionIds);
    formData.append('Action', 'right');
    this.closeRegisterService.actionsOnCardGroup(formData).subscribe({
      next: response => {
        if (response.isSuccess) {
          this.responseModal('success', 'The action done successfully');
          this.GetCurrentSessionCollectedCard();
        }
      }, error: err => {
        this.responseModal('error', err?.error?.errors);
      }
    }).add(() => this._spinnerService.hide())
  }
  responseModal(type, message) {
    const ref = this.modalService.open(ModalMessageComponent);
    ref.componentInstance.type = type;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }
  GetCurrentSessionCollectedCard() {
    return this.closeRegisterService
      .GetCurrentSessionCollectedCard(this.searchText, this.pageNo)
      .subscribe((response: any) => {
        if (response) {
          console.log(response);
          this.collectedData = response.data;
          this.totalAllRecordsCount = response.info?.totalAllRecordsCount;
          this.totalRecordsCount = response.info?.totalRecordsCount;
          this.pagin = Math.ceil(this.totalRecordsCount / 6);
          this.pages = _.range(this.pagin);
        }
      });
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.GetCurrentSessionCollectedCard();
    window.scroll(0, 0);
  }
  searchData(event: any) {
    console.log(event?.target.value);
    const text = event.target.value;
    console.log(text.length);
    if (text.length >= 3) {
      this.searchText = text;
      this.pageNo = 1;
      this.GetCurrentSessionCollectedCard();
    }
    if (text.length == 0) {
      this.pageNo = 1;
      this.GetCurrentSessionCollectedCard();
    }
  }
  removeSearch() {
    this.searchText = '';
    // this.sort = 1;
    this.GetCurrentSessionCollectedCard();
  }

  ActionsOnCard(item, type) {
    const formData = new FormData();
    formData.append('OrdersCardsCollectionId', item.ordersCardsCollectionId);
    formData.append('Action', type);
    console.log(formData);
    return this.closeRegisterService
      .ActionsOnCard(formData)
      .subscribe((response: any) => {
        if (response) {
          this.GetCurrentSessionCollectedCard();
          this.getCurrentSessionCollectedCardAll();
        } else {
          console.log('error', response);
        }
      });
  }
  ReverseActionsOnCard(item) {
    const formData = new FormData();
    formData.append('OrdersCardsCollectionId', item.ordersCardsCollectionId);
    console.log(formData);
    return this.closeRegisterService
      .ReverseActionsOnCard(formData)
      .subscribe((response: any) => {
        if (response) {
          this.GetCurrentSessionCollectedCard();
          this.getCurrentSessionCollectedCardAll();
        } else {
          console.log('error', response);
        }
      });
  }

  openModalEdit(item) {
    const modalRef = this.modalService.open(ModalEditCardComponent);
    modalRef.componentInstance.details = item;
    modalRef.componentInstance.loadPageData.subscribe((result: any) => {
      console.log('result', result);
      this.GetCurrentSessionCollectedCard();
      this.loadMainData.emit();
    });
  }
}
