import { DatePipe } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { CollectService } from '../../../collect/services/collect.service';
import { CloseRegisterService } from '../../services/close-register.service';

@Component({
  selector: 'app-modal-edit-card',
  templateUrl: './modal-edit-card.component.html',
  styleUrls: ['./modal-edit-card.component.scss'],
  providers: [DatePipe],
})
export class ModalEditCardComponent {
  @Input() details;
  formGroup: FormGroup;
  @Output() loadMainData = new EventEmitter<string>();
  type: string;
  message: string;
  terminals = [];
  @Output() loadPageData = new EventEmitter<string>();
  constructor(
    public closeRegisterService: CloseRegisterService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private fb: FormBuilder,
    private _collectService: CollectService,
    public datePipe: DatePipe,
  ) { }
  ngOnInit(): void {
    this.getForm();
    this.getTerminalProviders();
    if (this.details) {
      let obj = {
        OrdersCardsCollectionId: this.details.ordersCardsCollectionId,
        LastTerminalId: this.details?.cardProvide?.providerId,
        CardDrawerName: this.details?.cardPayerName,
        LastCollectedAmount: this.details.lastCollectedAmount,
        MachineReceiptNumber: this.details.machineReceiptNumber,
        CardDate: this.datePipe.transform(this.details.collectionAt, 'yyyy-MM-dd'),
        CardLast4Digits: this.details?.cardLast4Digits,
      }
      this.formGroup.patchValue(obj)
    }
  }
  getTerminalProviders() {
    this._collectService.getTerminalProviders().subscribe((response: any) => {
      this.terminals = response.data.map(x => {
        return {
          id: x.providerId,
          name: x.providerName,
        }
      })
    })
  }
  getForm() {
    this.formGroup = this.fb.group({
      OrdersCardsCollectionId: 0,
      LastTerminalId: [''],
      LastCollectedAmount: [''],
      CardDrawerName: '',
      MachineReceiptNumber: [''],
      CardDate: [''],
      CardLast4Digits: [''],
    });
  }

  get formValid(): { [key: string]: AbstractControl } {
    return this.formGroup.controls;
  }

  onSubmit() {
    if (this.formGroup.invalid) {
      this.formGroup.markAllAsTouched();
      return window.scroll(0, 0);
    }
    const values = this.formGroup.value;
    const formData = new FormData();
    formData.append('OrdersCardsCollectionId', values?.OrdersCardsCollectionId);
    formData.append('LastTerminalId', values?.LastTerminalId);
    formData.append('CardDrawerName', values?.CardDrawerName);
    formData.append('MachineReceiptNumber', values?.MachineReceiptNumber);
    formData.append('CardLast4Digits', values?.CardLast4Digits);
    return this.closeRegisterService.EditCollectionByCards(formData).subscribe(
      (response: any) => {
        console.log(response);

        if (response.isSuccess) {
          this.type = 'success';
          this.message = 'Edit done successfully';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.toLoadData();
          }, 2000);
          console.log(response);
        } else {
        }
      },
      (error: any) => {
      }
    );
  }
  toLoadData() {
    this.loadPageData.emit();
  }
}
