import { DatePipe } from '@angular/common';
import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { RegisterSettlementService } from '../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { ViewFileComponent } from '../../../collect/payment-modal/view-file/view-file.component';
import { CloseRegisterService } from '../../services/close-register.service';

@Component({
  selector: 'app-modal-edit',
  templateUrl: './modal-edit.component.html',
  styleUrls: ['./modal-edit.component.scss'],
  providers: [DatePipe],
})
export class ModalEditComponent implements OnInit {
  @Input() details;
  formGroup: FormGroup;
  @Output() loadMainData = new EventEmitter<string>();
  @Output() loadPageData = new EventEmitter<string>();
  type: string;
  message: string;
  submitted: boolean;
  //file
  uploadedFile;
  enter: boolean;
  fileName = '';
  invalidFileType = '';
  @ViewChild('fileInput') inputFile: ElementRef<any>;
  constructor(
    public closeRegisterService: CloseRegisterService,
    public _registerSettlementService: RegisterSettlementService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private fb: FormBuilder,
    public datePipe: DatePipe,
    private router: Router,
    private _commonService: CommonService
  ) { }
  ngOnInit(): void {
    this.getForm();
    this.getBanks();

    if (this.details) {
      console.log(this.details);
      this.formGroup.get('ChequeCollectionId').patchValue(this.details.chequeCollectionId);
      this.formGroup
        .get('ChequeCollectionJod')
        .patchValue(this._commonService.transformDecimal(this.details.chequeCollectionJod));
      this.formGroup.get('BankId').patchValue(this.details.bankId);
      this.formGroup
        .get('ChequeDrawerName')
        .patchValue(this.details.chequeDrawerName);
      this.formGroup.get('ChequeNo').patchValue(this.details.chequeNo);
      this.formGroup
        .get('ChequeDate')
        .patchValue(
          this.datePipe.transform(this.details.chequeDate, 'yyyy-MM-dd')
        );
    }
  }
  getForm() {
    this.formGroup = this.fb.group({
      ChequeCollectionId: 0,
      ChequeCollectionJod: [''],
      ChequeDate: [''],
      BankId: '',
      ChequeDrawerName: [''],
      ChequeNo: ['', [Validators.required, Validators.pattern('^[0-9]*$')]],
    });
  }

  get formValid(): { [key: string]: AbstractControl } {
    return this.formGroup.controls;
  }

  onSubmit() {
    this.submitted = true;
    console.log(this.submitted);
    if (this.formGroup.invalid) {
      this.formGroup.markAllAsTouched();
      return window.scroll(0, 0);
    }
    const formData = new FormData();
    formData.append(
      'ChequeCollectionId',
      this.formGroup.get('ChequeCollectionId')?.value
    );
    formData.append(
      'ChequeCollectionJod',
      this.formGroup.get('ChequeCollectionJod')?.value
    );
    formData.append('ChequeDate', this.formGroup.get('ChequeDate')?.value);
    formData.append('BankId', this.formGroup.get('BankId')?.value);
    formData.append(
      'ChequeDrawerName',
      this.formGroup.get('ChequeDrawerName')?.value
    );
    formData.append('ChequeNo', this.formGroup.get('ChequeNo')?.value);

    console.log(this.formGroup.value);
    console.log(formData);
    return this.closeRegisterService.EditCollectionCheuqe(formData).subscribe(
      (response: any) => {
        console.log(response);

        if (response.isSuccess == true) {
          this.type = 'success';
          this.message = 'Edit done successfully';
          if (this.uploadedFile?.file) {
            this.addSettleCheckImage();
          }
          setTimeout(() => {
            this.modalService.dismissAll();
            this.toLoadData();
          }, 2000);

          console.log(response);
        } else {
          // console.log('error', response);
        }
      },
      (error: any) => {
        // this.isLoading = false;
        // console.log('error', error);
      }
    );
  }
  toLoadData() {
    this.loadPageData.emit();
  }
  banks: any;
  getBanks() {
    this.closeRegisterService.getLookupsById(11).subscribe(
      (response: any) => {
        this.banks = response.data.map((x) => ({
          id: x.id,
          name: x.name[0].lookupName,
        }));
      },
      (error) => {
        // this.errorMessage = error.message;
      }
    );
  }
  addSettleCheckImage() {
    let formData = new FormData();
    formData.append('chequeCollectionId', this.formGroup.controls['ChequeCollectionId'].value);
    formData.append('Image', this.uploadedFile?.file);
    this._registerSettlementService.addSettledChequesImages(formData).subscribe(response => {
      console.log(response);
    })
  }
  //FILE 
  fileAction(action) {
    if (action == 'view') {
      const modelRef = this.modalService.open(ViewFileComponent, { size: 'md', })
      modelRef.componentInstance.imageUploadedView = this.uploadedFile;
    }
    else {
      this.inputFile.nativeElement.value = null;
      //(document.getElementById('chequeFile') as HTMLInputElement).value = null;
      this.fileName = '';
      this.uploadedFile = {};
    }
  }
  filesDropped(data: any) {
    this.enter = false;
    let isInvalid = this._commonService.checkInvalidImageFormat(data, true).invalidExtentions;
    this.invalidFileType = this._commonService.checkInvalidImageFormat(data, true).invalidError;
    if (isInvalid) {
      this.inputFile.nativeElement.value = null;
      return;
    }
    let files = data.map(x => {
      return x.file
    })
    if (!isInvalid) {
      files.forEach((file: File) => {
        this.fileName = file.name;
        this._commonService.fileToBase64(file).then(response => {
          this.uploadedFile = response;
        })
      })
    }
  }

  onSelectFile(event: any) {
    let files = [...event.target.files];
    let isInvalid = this._commonService.checkInvalidImageFormat(files, true).invalidExtentions;
    this.invalidFileType = this._commonService.checkInvalidImageFormat(files, true).invalidError;
    if (isInvalid) {
      this.inputFile.nativeElement.value = null;
      return;
    }
    if (files.length > 0) {
      if (!isInvalid) {
        files.forEach((file: File) => {
          this.fileName = file.name;
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
          })
        })
      }
    }
  }
  removeFile(item) {
    this.inputFile.nativeElement.value = null;
    this.fileName = '';
    this.uploadedFile = null;
  }
}
